table {
    width: 100%;
    border-collapse: collapse;
}

th{
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    border-bottom: 1px solid #ddd;
}

td {
    font-size: 17px;
    color:#787878;
    padding:12px 10px;
    text-align: center;
    border-bottom: 1px solid #ddd;
}